/* Modal header (question part) */
.modal-content h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #333;
}

/* Modal description (event name) */
.modal-content p {
  font-size: 1rem;
  margin-bottom: 20px;
  color: #555;
}

/* Modal action buttons */
.modal-actions {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

/* Button styling */
button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.modal-buttons-rsvp {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.confirm-button-rsvp,
.cancel-button-rsvp {
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.confirm-button-rsvp {
  background-color: var(--primary);
  color: white;
}

.cancel-button {
  background-color: var(--accent-white);
  color: black;
  border: 1px solid black;
}
@media (max-width: 768px) {
  .modal-content-unrsvp {
    width: 315px;
  }
}
